
import Layout from "hocs/layouts/Layout"
import Navbar from "components/navigation/Navbar"
import Footer from "components/navigation/Footer"
import CardDownload from "components/Nuestra Fede/CardDownload"
import { motion } from 'framer-motion';
import re333 from "assets/docs/sanitarias/RESOLUCIN_333_DE_2011.pdf"
import re779 from "assets/docs/sanitarias/RESOLUCIN_779_DE_2006.pdf"
import re3462 from "assets/docs/sanitarias/RESOLUCIN_3462_DE_2008.pdf"
import re3544 from "assets/docs/sanitarias/RESOLUCIN_3544_DE_2009.pdf"
import re4121 from "assets/docs/sanitarias/RESOLUCIN_4121_DE_2011.pdf"
import re5109 from "assets/docs/sanitarias/RESOLUCIN_5109_DE_2005.pdf"
import re16379 from "assets/docs/sanitarias/RESOLUCIN_16379_DE_2003.pdf"


function createData(titulo, cuerpo, documento) {
    return { titulo, cuerpo, documento };
}

const rows = [
    createData('Resolucin 16379 de 2003', "Por la cual se reglamenta el control metrolgico y del contenido de producto en pre-empacados. (ms)", re16379),
    createData('Resolucin 5109 de 2005', "Por el cual se establece el reglamento tcnico sobre los requisitos de rotulado o etiquetado que deben cumplir los alimentos envasados y materias primas de alimentos para consumo humano. (ms)", re5109),
    createData('Resolucin 333 de 2011', "Por la cual se establece el reglamento tcnico sobre los requisitos de rotulado o etiquetado nutricional que deben cumplir los alimentos envasados para consumo humano. (ms)", re333),
    createData('Resolucin 779 de 2006', "Por la cual se establece el reglamento tcnico sobre los requisitos sanitarios que se deben cumplir en la produccin y comercializacin de la panela para consumo humano y se dictan otras disposiciones. (ms)", re779),
    createData('Resolucin 3462 de 2008', "Por el cual se modifica el artculo 9 de las condiciones sanitarias de los trapiches y el artculo 15 sobre requisitos para la exportacin de panela de la Resolucin 779 de 2006 y se dictan otras disposiciones. (ms)", re3462),
    createData('Resolucin 3544 de 2009', "Por el cual se modifica el artculo 11 y el artculo 13 de la Resolucin 779 de 2006, sobre envase y rotulado respectivamente. (ms)", re3544),
    createData('Resolucin 4121 de 2011', "Por el cual se modifica parcialmente la Resolucin 779 de 2006, modificadas por las Resoluciones 3462 de 2008 y 3544 de 2009. (ms)", re4121),

];

function Sanitarias() {

    return (
        <Layout>

            <Navbar />

            <div className="container" style={{ marginTop: "100px", marginBottom: "50px" }}>

                <h1 className="text-center h1-title mt-5">Normatividad Sanitarias</h1>
                <div className="row mt-5">

                    {rows.map((row) => (
                        <div className="col-md-4 mt-5">
                            <motion.div
                                initial={{ opacity: 0, x: -100 }} // Inicia con opacidad 0 y posicin x -100
                                animate={{ opacity: 1, x: 0 }} // Anima a opacidad 1 y posicin x 100
                                transition={{ duration: 0.9, ease: "easeOut" }}
                            >
                                <CardDownload titulo={row.titulo} doc={row.documento} cuerpo={row.cuerpo} />
                            </motion.div>
                        </div>
                    ))}

 

                </div>


            </div>



            <Footer />


        </Layout>
    )
}
export default Sanitarias